import React from 'react';
import { Heading, Pane } from 'evergreen-ui';

const Subtitle = ({ title = '', sub = '', id }) => (
  <Pane
    id={id}
    flexDirection="column"
    marginTop="default"
    paddingBottom="15px"
    marginBottom="40px"
  >
    <Pane display="flex" justifyContent="center">
      <Heading size={800}>{title.toUpperCase()}</Heading>
    </Pane>
    <Pane display="flex" justifyContent="center">
      <Heading size={200}>{sub}</Heading>
    </Pane>
  </Pane>
);

export default Subtitle;
