import React from 'react';
import { graphql } from 'gatsby';
import rehypeReact from 'rehype-react';
import Layout from '../components/layout';
import SEO from '../components/seo';
import { Heading, Pane, Paragraph, Text } from 'evergreen-ui';
import Subtitle from '../components/subtitle';

class WorkTemplate extends React.Component {
  render() {
    const work = this.props.data.markdownRemark;
    const siteTitle = this.props.data.site.siteMetadata.title;

    const renderAst = new rehypeReact({
      createElement: React.createElement,
      components: {
        h1: (props) => (
          <Heading textAlign="center" size={800} {...props}></Heading>
        ),
        h2: (props) => (
          <Heading textAlign="center" size={700} {...props}></Heading>
        ),
        h3: (props) => (
          <Heading textAlign="center" size={600} {...props}></Heading>
        ),
        p: (props) => (
          <Paragraph
            size={500}
            textAlign="justify"
            marginBottom="16px"
            {...props}
          />
        ),
        figcaption: (props) => <Paragraph textAlign="center" {...props} />,
      },
    }).Compiler;

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <Pane
          marginRight="50px"
          marginLeft="50px"
          marginBottom="100px"
        >
          <SEO
            title={work.frontmatter.title}
            description={work.frontmatter.description || work.excerpt}
          />
          {/* <Subtitle title="WORK" sub="Narrative of the work üßë‚Äçüé®" /> */}
          <Pane display="flex" flexWrap="wrap">
            <Pane
              display="flex"
              flexDirection="column"
              alignItems="center"
              flex="38.2"
              minWidth="350px"
              marginBottom="64px"
            >
              <Subtitle
                title={work.frontmatter.title}
                sub={work.frontmatter.category}
              />
              <Pane display="flex" flexDirection="column">
                <a href={work.frontmatter.github} target="_blank">
                  <Text color="#429777">üêô source</Text>
                </a>
                {work.frontmatter.demo && (
                  <a href={work.frontmatter.demo} target="_blank">
                    <Text color="#429777">ü§ñ Demo</Text>
                  </a>
                )}
              </Pane>
            </Pane>

            <Pane flex="61.8">{renderAst(work.htmlAst)}</Pane>
          </Pane>
        </Pane>
      </Layout>
    );
  }
}

export default WorkTemplate;

export const pageQuery = graphql`
  query WorkBySlug($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      htmlAst
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        category
        demo
        github
      }
    }
  }
`;
